<template>


  <div class="hello">

    <transition name="fade">

      <SuccessComponent v-bind:f_count="this.successCount" v-bind:display_text="this.successMessageBind"
        v-if="this.successMessageBind" />
    </transition>


    <h1>My Frustrations</h1>
    <p>
      Use the frustrations below to document when they really get to you. It's okay to be frustrated, but when you click
      the button and remind yourself that the problem is being dealt with, or see if you can come up with a solution to
      the problem. <br><br><b>More importantly,</b> remember to congratulate yourself when you have completed fixing the
      problem.
    </p>

    <p style="font-size: smaller; color: rgb(53, 101, 117);">Frustrations are only kept locally on this device and are
      never
      transmitted!</p>


  </div>

  <div class="hello" style="margin-top: 10px;">
    <p style="display: inline-flex;">üò¨ Active Frustrations: {{ this.allFustrations }}</p>
    <p style="display: inline-flex;">&nbsp;<b>||</b>&nbsp; üõ†Ô∏è Fixed Frustrations: {{ this.fixedFustrations }}</p>
    <button class="hello" style="float: right; display; margin-top: 5px; " v-on:click="this.addNewFrust()">
      ‚ûï Add a Frustration
    </button>
    <input v-model="this.newFrustInputBind" type="text" placeholder="Enter frustration" style="font-size: 0.8em; border-radius: 7px; margin-top: 5px;
    padding: 10px; min-width: 30%; float: right; margin-right: 10px;">

  </div>

  <div style="display: inline-flexbox" v-if="!this.successMessageBind">
    <div v-if="this.loadedFustrations" class="hello" style="margin-top: 15px;">
      <FustrationComponent v-for="[key, item] in Object.entries(loadedFustrations)" v-bind:key="key"
        v-bind:f_count="item.frustration_count" v-bind:unique_id="key" v-bind:display_text="item.display_text"
        @increaseFCount="updateFCount(key)" @completedTask="showSuccessMsg(key)">
      </FustrationComponent>

    </div>
  </div>


</template>

<script>
  import FustrationComponent from './FustrationComponent.vue';
  import SuccessComponent from './SuccessComponent.vue';
  export default {
    name: 'HelloWorld',
    props: {
      msg: String
    },
    data: () => ({
      successCount: 0,
      successMessageBind: "",
      newFrustInputBind: "",
      allFustrations: 0,
      fixedFustrations: 0,
      loadedFustrations: {
        "blah": {
          display_text: "blah blah blah",
          frustration_count: 10,
          created_epoch: 10000
        }
      }
    }),
    components: {
      FustrationComponent,
      SuccessComponent
    },
    created: function () {
      this.loadStorage();
    },
    methods: {
      updateFCount: function (id) {
        this.loadedFustrations[id]['frustration_count'] += 1

        var countFrust = 0
        for (var key in this.loadedFustrations) {
          var value = this.loadedFustrations[key];
          countFrust+= value['frustration_count']
        }
        this.allFustrations = countFrust

        this.updateStorage()
      },
      showSuccessMsg: async function (id) {
        // start animation by setting a v-bind value
        this.successCount = this.loadedFustrations[id]['frustration_count']
        this.successMessageBind = this.loadedFustrations[id]['display_text']
        await new Promise(r => setTimeout(r, 4000));

        // update local tracker
        this.fixedFustrations+=this.successCount
        localStorage.setItem("fixedFustrations", this.fixedFustrations.toString())

        this.successCount = 0
        this.successMessageBind = ""
        // end animation 




        // update local var
        delete this.loadedFustrations[id]
        this.updateStorage()
      },

      addNewFrust: function () {
        console.log("adding item")

        if (!(this.newFrustInputBind)) {
          return
        }

        let rand = Math.floor(Math.random() * Date.now())
        let newKey = rand.toString()

        let newObj = {
          display_text: this.newFrustInputBind,
          frustration_count: 0,
          created_epoch: Date.now()
        }

        console.log(newObj)

        // update the dictionary and then reset the input
        this.loadedFustrations[newKey] = new Object()
        this.loadedFustrations[newKey] = newObj
        this.newFrustInputBind = ""
        this.updateStorage()

        console.log(Object(this.loadedFustrations))

      },

      updateStorage() {
        localStorage.setItem("FustrateList", JSON.stringify(this.loadedFustrations));
        this.loadStorage()
      },
      loadStorage() {
        this.loadedFustrations = JSON.parse(localStorage.getItem("FustrateList"))
        this.fixedFustrations = Number(localStorage.getItem("fixedFustrations")) || 0
        if (this.loadedFustrations === null) {
          this.loadedFustrations = new Object()
        } // catch no saved frustrations
        // update total count
        this.allFustrations = 0
        for (var key in this.loadedFustrations) {
          var value = this.loadedFustrations[key];
          this.allFustrations+= value['frustration_count']
        }
      }
    }
  }
</script>

